@media (min-width: 740px) and (max-width: 995px) {
    .wrapper {
        padding: 55px 40px 10px 40px;
    }
}

@media (max-width: 740px) {
    .wrapper {
        padding: 55px 20px 10px 20px;
    }
}

@media (min-width: 995px) and (max-width: 1500px) {
    .news-latest {
        margin-top: 40px;
        & .title-recent-review {
            font-size: 2rem;
        }

        & img {
            width: 260px !important;
        }
        & h3 {
            font-size: 1.8rem;
        }
    }
}

.news-latest {
    margin-top: 40px;
}
