    margin: 10px 20px;
    z-index: 10;
}

aside.ant-layout-sider.ant-layout-sider-light.sider-primary.ant-layout-sider-primary.active-route a.active {
    background: transparent;
    box-shadow: none;
}
.uploadfile.pb-15 {
    padding-bottom: 16px;
}

.uploadfile.shadow-none .ant-upload.ant-upload-select.ant-upload-select-text .ant-btn.ant-full-box {
    box-shadow: none;
}

.ant-btn-dashed {
    color: rgba(0, 0, 0, 0.65);
}
button.ant-btn.ant-btn-dashed.ant-full-box svg {
    width: 16px;
    height: 16px;
}

.ant-btn-dashed.active,
.ant-btn-dashed:active {
    color: #096dd9;
    background-color: #fff;
    border-color: #096dd9;
}

section.ant-layout.ant-layout-has-sider.layout-dashboard.layout-profile
    .header-control
    span.ant-input-affix-wrapper.header-search
    .anticon-search
    svg
    path {
    fill: #8c8c8c;
}

.ant-modal-footer {
    .ant-btn-primary {
        background-color: #ff4d4f;
    }
}

.ant-message-notice-content {
    margin-top: 50px;
}
.modal-info-detail {
    & .ant-modal-footer {
        & .ant-btn-default {
            display: none;
        }
    }
}

// custom label in product details
.wrapper-custom {
    & .ant-form-item-label {
        line-height: 20px !important;
        white-space: normal;
        text-align: center;
        flex: none;
        max-width: none;
    }
}
